// tests/integration/components/FacturesListe.test.jsx
// Tests d'intégration pour le composant FacturesListe

import React from 'react';
import { renderWithProviders, screen, waitFor, expectTableRows, changeFilter } from '../../utils/testUtils';
import { mockFactureService, resetAllMocks } from '../../mocks/services';

// Mock des hooks
jest.mock('@components/factures/hooks/useFactureActions', () => ({
  useFactureActions: () => ({
    chargerFactures: mockFactureService.chargerFactures,
    supprimerFacture: mockFactureService.supprimerFacture,
    annulerFacture: mockFactureService.annulerFacture,
    imprimerFacture: mockFactureService.imprimerFacture,
    envoyerFactureParEmail: mockFactureService.envoyerFactureParEmail,
    enrichirFacturesAvecEtat: (factures) => factures.map(f => ({
      ...f,
      etatAffichage: f.etat
    }))
  })
}));

// Import après les mocks
import FacturesListe from '@components/factures/FacturesListe';

describe('FacturesListe', () => {
  const defaultProps = {
    facturesNonFiltrees: [
      createTestFacture({ idFacture: 1, numeroFacture: '001.2025', etat: 'En attente' }),
      createTestFacture({ idFacture: 2, numeroFacture: '002.2025', etat: 'Payée' }),
      createTestFacture({ idFacture: 3, numeroFacture: '003.2025', etat: 'Annulée' }),
    ],
    anneeSelectionnee: 2025,
    setAnneeSelectionnee: jest.fn(),
    onModifierFacture: jest.fn(),
    onAfficherFacture: jest.fn(),
    onNouvelleFacture: jest.fn(),
    onFactureSupprimee: jest.fn(),
    notification: { message: '', type: '' },
    onClearNotification: jest.fn(),
    chargerFactures: jest.fn()
  };

  beforeEach(() => {
    resetAllMocks();
    jest.clearAllMocks();
  });

  describe('Affichage de la liste', () => {
    it('devrait afficher le tableau des factures', () => {
      renderWithProviders(<FacturesListe {...defaultProps} />);
      
      expect(screen.getByRole('table')).toBeInTheDocument();
    });

    it('devrait afficher toutes les factures', () => {
      renderWithProviders(<FacturesListe {...defaultProps} />);
      
      expect(screen.getByText('001.2025')).toBeInTheDocument();
      expect(screen.getByText('002.2025')).toBeInTheDocument();
      expect(screen.getByText('003.2025')).toBeInTheDocument();
    });

    it('devrait afficher le nombre de factures', () => {
      renderWithProviders(<FacturesListe {...defaultProps} />);
      
      expect(screen.getByText(/3.*factures?/i)).toBeInTheDocument();
    });

    it('devrait afficher un message si aucune facture', () => {
      renderWithProviders(<FacturesListe {...defaultProps} facturesNonFiltrees={[]} />);
      
      expect(screen.getByText(/aucune facture/i)).toBeInTheDocument();
    });
  });

  describe('Filtres', () => {
    it('devrait afficher les filtres', () => {
      renderWithProviders(<FacturesListe {...defaultProps} />);
      
      expect(screen.getByLabelText(/année/i)).toBeInTheDocument();
      expect(screen.getByLabelText(/client/i)).toBeInTheDocument();
      expect(screen.getByLabelText(/état/i)).toBeInTheDocument();
    });

    it('devrait filtrer par état', async () => {
      const { user } = renderWithProviders(<FacturesListe {...defaultProps} />);
      
      const etatFilter = screen.getByLabelText(/état/i);
      await user.selectOptions(etatFilter, 'Payée');
      
      await waitFor(() => {
        expect(screen.getByText('002.2025')).toBeInTheDocument();
        expect(screen.queryByText('001.2025')).not.toBeInTheDocument();
      });
    });

    it('devrait filtrer par client', async () => {
      const facturesAvecClients = [
        createTestFacture({ idFacture: 1, client: { idClient: 1, nom: 'Dupont' } }),
        createTestFacture({ idFacture: 2, client: { idClient: 2, nom: 'Martin' } }),
      ];
      
      const { user } = renderWithProviders(
        <FacturesListe {...defaultProps} facturesNonFiltrees={facturesAvecClients} />
      );
      
      const clientFilter = screen.getByLabelText(/client/i);
      await user.selectOptions(clientFilter, '1');
      
      await waitFor(() => {
        expect(screen.getByText('Dupont')).toBeInTheDocument();
        expect(screen.queryByText('Martin')).not.toBeInTheDocument();
      });
    });

    it('devrait changer d\'année', async () => {
      const { user } = renderWithProviders(<FacturesListe {...defaultProps} />);
      
      const anneeFilter = screen.getByLabelText(/année/i);
      await user.selectOptions(anneeFilter, '2024');
      
      expect(defaultProps.setAnneeSelectionnee).toHaveBeenCalledWith(2024);
    });

    it('devrait réinitialiser les filtres', async () => {
      const { user } = renderWithProviders(<FacturesListe {...defaultProps} />);
      
      // Appliquer un filtre
      const etatFilter = screen.getByLabelText(/état/i);
      await user.selectOptions(etatFilter, 'Payée');
      
      // Réinitialiser
      const resetButton = screen.getByRole('button', { name: /réinitialiser/i });
      await user.click(resetButton);
      
      await waitFor(() => {
        expect(etatFilter.value).toBe('Tous');
      });
    });
  });

  describe('Actions sur les factures', () => {
    it('devrait permettre de visualiser une facture', async () => {
      const { user } = renderWithProviders(<FacturesListe {...defaultProps} />);
      
      const viewButtons = screen.getAllByRole('button', { name: /voir|visualiser/i });
      await user.click(viewButtons[0]);
      
      expect(defaultProps.onAfficherFacture).toHaveBeenCalledWith(1);
    });

    it('devrait permettre de modifier une facture', async () => {
      const { user } = renderWithProviders(<FacturesListe {...defaultProps} />);
      
      const editButtons = screen.getAllByRole('button', { name: /modifier/i });
      await user.click(editButtons[0]);
      
      expect(defaultProps.onModifierFacture).toHaveBeenCalledWith(1);
    });

    it('devrait permettre de supprimer une facture', async () => {
      const { user } = renderWithProviders(<FacturesListe {...defaultProps} />);
      
      const deleteButtons = screen.getAllByRole('button', { name: /supprimer/i });
      await user.click(deleteButtons[0]);
      
      // Modal de confirmation
      await waitFor(() => {
        expect(screen.getByText(/confirmer.*suppression/i)).toBeInTheDocument();
      });
      
      // Confirmer
      const confirmButton = screen.getByRole('button', { name: /confirmer/i });
      await user.click(confirmButton);
      
      await waitFor(() => {
        expect(mockFactureService.supprimerFacture).toHaveBeenCalledWith(1);
      });
    });

    it('devrait permettre d\'annuler la suppression', async () => {
      const { user } = renderWithProviders(<FacturesListe {...defaultProps} />);
      
      const deleteButtons = screen.getAllByRole('button', { name: /supprimer/i });
      await user.click(deleteButtons[0]);
      
      // Annuler
      const cancelButton = screen.getByRole('button', { name: /annuler/i });
      await user.click(cancelButton);
      
      expect(mockFactureService.supprimerFacture).not.toHaveBeenCalled();
    });

    it('devrait permettre d\'imprimer une facture', async () => {
      const { user } = renderWithProviders(<FacturesListe {...defaultProps} />);
      
      const printButtons = screen.getAllByRole('button', { name: /imprimer/i });
      await user.click(printButtons[0]);
      
      await waitFor(() => {
        expect(mockFactureService.imprimerFacture).toHaveBeenCalledWith(1);
      });
    });

    it('devrait permettre d\'envoyer une facture par email', async () => {
      const { user } = renderWithProviders(<FacturesListe {...defaultProps} />);
      
      const emailButtons = screen.getAllByRole('button', { name: /email|envoyer/i });
      await user.click(emailButtons[0]);
      
      // Modal d'email
      await waitFor(() => {
        expect(screen.getByText(/envoyer.*email/i)).toBeInTheDocument();
      });
    });
  });

  describe('Tri', () => {
    it('devrait trier par numéro de facture', async () => {
      const { user } = renderWithProviders(<FacturesListe {...defaultProps} />);
      
      const numeroHeader = screen.getByRole('columnheader', { name: /numéro/i });
      await user.click(numeroHeader);
      
      // Vérifier l'ordre
      const rows = screen.getAllByRole('row');
      expect(rows[1]).toHaveTextContent('001.2025');
    });

    it('devrait trier par date', async () => {
      const { user } = renderWithProviders(<FacturesListe {...defaultProps} />);
      
      const dateHeader = screen.getByRole('columnheader', { name: /date/i });
      await user.click(dateHeader);
      
      // Le tri devrait être appliqué
      expect(screen.getAllByRole('row').length).toBeGreaterThan(1);
    });

    it('devrait inverser l\'ordre de tri au second clic', async () => {
      const { user } = renderWithProviders(<FacturesListe {...defaultProps} />);
      
      const numeroHeader = screen.getByRole('columnheader', { name: /numéro/i });
      
      // Premier clic - tri ascendant
      await user.click(numeroHeader);
      
      // Second clic - tri descendant
      await user.click(numeroHeader);
      
      const rows = screen.getAllByRole('row');
      expect(rows[1]).toHaveTextContent('003.2025');
    });
  });

  describe('Pagination', () => {
    it('devrait afficher la pagination si beaucoup de factures', () => {
      const manyFactures = Array.from({ length: 25 }, (_, i) => 
        createTestFacture({ 
          idFacture: i + 1, 
          numeroFacture: `${String(i + 1).padStart(3, '0')}.2025` 
        })
      );
      
      renderWithProviders(<FacturesListe {...defaultProps} facturesNonFiltrees={manyFactures} />);
      
      expect(screen.getByRole('navigation', { name: /pagination/i })).toBeInTheDocument();
    });

    it('devrait changer de page', async () => {
      const manyFactures = Array.from({ length: 25 }, (_, i) => 
        createTestFacture({ 
          idFacture: i + 1, 
          numeroFacture: `${String(i + 1).padStart(3, '0')}.2025` 
        })
      );
      
      const { user } = renderWithProviders(
        <FacturesListe {...defaultProps} facturesNonFiltrees={manyFactures} />
      );
      
      const nextButton = screen.getByRole('button', { name: /suivant|next/i });
      await user.click(nextButton);
      
      // Vérifier que la page a changé
      expect(screen.getByText(/page 2/i)).toBeInTheDocument();
    });
  });

  describe('Notifications', () => {
    it('devrait afficher une notification de succès', () => {
      renderWithProviders(
        <FacturesListe 
          {...defaultProps} 
          notification={{ message: 'Facture créée avec succès', type: 'success' }} 
        />
      );
      
      expect(screen.getByText('Facture créée avec succès')).toBeInTheDocument();
    });

    it('devrait afficher une notification d\'erreur', () => {
      renderWithProviders(
        <FacturesListe 
          {...defaultProps} 
          notification={{ message: 'Erreur lors de la suppression', type: 'error' }} 
        />
      );
      
      expect(screen.getByText('Erreur lors de la suppression')).toBeInTheDocument();
    });

    it('devrait permettre de fermer la notification', async () => {
      const { user } = renderWithProviders(
        <FacturesListe 
          {...defaultProps} 
          notification={{ message: 'Test notification', type: 'success' }} 
        />
      );
      
      const closeButton = screen.getByRole('button', { name: /fermer/i });
      await user.click(closeButton);
      
      expect(defaultProps.onClearNotification).toHaveBeenCalled();
    });
  });

  describe('États visuels', () => {
    it('devrait afficher un badge pour les factures en retard', () => {
      const facturesAvecRetard = [
        createTestFacture({ 
          idFacture: 1, 
          etat: 'En attente',
          etatAffichage: 'Retard',
          dateEcheance: '2024-01-01'
        })
      ];
      
      renderWithProviders(
        <FacturesListe {...defaultProps} facturesNonFiltrees={facturesAvecRetard} />
      );
      
      expect(screen.getByText('Retard')).toHaveClass('badge-retard');
    });

    it('devrait afficher un badge pour les factures payées', () => {
      renderWithProviders(<FacturesListe {...defaultProps} />);
      
      expect(screen.getByText('Payée')).toHaveClass('badge-payee');
    });

    it('devrait afficher un badge pour les factures annulées', () => {
      renderWithProviders(<FacturesListe {...defaultProps} />);
      
      expect(screen.getByText('Annulée')).toHaveClass('badge-annulee');
    });
  });

  describe('Bouton Nouvelle Facture', () => {
    it('devrait afficher le bouton Nouvelle Facture', () => {
      renderWithProviders(<FacturesListe {...defaultProps} />);
      
      expect(screen.getByRole('button', { name: /nouvelle facture/i })).toBeInTheDocument();
    });

    it('devrait appeler onNouvelleFacture au clic', async () => {
      const { user } = renderWithProviders(<FacturesListe {...defaultProps} />);
      
      const newButton = screen.getByRole('button', { name: /nouvelle facture/i });
      await user.click(newButton);
      
      expect(defaultProps.onNouvelleFacture).toHaveBeenCalled();
    });
  });
});